import { helpImage, helpPlaylist } from './help.js';
import { useEffect, useState, useRef } from 'react';

import './App.css';

const querystring = require('querystring');


function generateRandomString(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

function App() {
    const CLIENT_ID = '550d45a27d884184a09e9d159cc04697';
    const REDIRECT_URI = 'https://jtan2231.github.io/';
    const AUTH_ENDPOINT = 'https://accounts.spotify.com/authorize';
    const RESPONSE_TYPE = 'code';
    const QUERY_PARAMS = querystring.stringify({
        response_type: 'token',
        client_id: CLIENT_ID,
        scope: 'user-top-read playlist-modify-public',
        redirect_uri: REDIRECT_URI,
        state: generateRandomString(16),
    });

    const API_BASE = 'https://api.spotify.com/v1/';

    const top3 = 'your top 3 songs';
    const upToThree = '1 - 3 songs';
    const aPlaylist = 'a playlist'

    const [token, setToken] = useState('');
    const [userid, setUserid] = useState('');
    const [topTracks, setTopTracks] = useState([]);

    const [response, setResponse] = useState('');

    const [helpToggle, setHelp] = useState(false);
    const [seedType, setSeedType] = useState(top3);

    const limitInput = useRef(null);
    const seedTypeInput = useRef(null);
    const titleInput = useRef(null);

    const createInput = (ref) => {
        return (<input ref={ ref } style={{ width: '11em' }} type="text" placeholder="spotify song link" />);
    }

    const customSongInputRefs = [useRef(null), useRef(null), useRef(null)];
    const customSongInputs = [createInput(customSongInputRefs[0]),
                              createInput(customSongInputRefs[1]),
                              createInput(customSongInputRefs[2])];

    const customPlaylistRef = useRef(null);

    const getAuthHeaders = (t) => {
        return { Authorization: `Bearer ${t}` };
    }

    useEffect(() => {
        const params = new Proxy(new URLSearchParams(window.location.hash), {
            get: (searchParams, prop) => searchParams.get(prop),
        });

        setToken(params['#access_token']);
    }, [])

    const logout = () => {
        setToken('');
    }

    const getUser = async (trackuris) => {
        fetch(`${API_BASE}me`, {
            method: 'GET',
            headers: getAuthHeaders(token),
        }).then(res => res.json()).then(res => {
            setUserid(res.id);
            createPlaylist(res.id, trackuris);
        });
    }

    const getToday = () => {
        let today = new Date();
        let dd = String(today.getDate()).padStart(2, '0');
        let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        let yyyy = today.getFullYear();

        return mm + '/' + dd + '/' + yyyy;
    }

    const getIDFromLinks = (links) => {
        let ids = links.map(link => link.split('/'));
        ids = ids.map(id => id[id.length-1]);
        ids = ids.map(id => id.split('?')[0]);

        return ids;
    }

    const getCustomInputs = (refs) => {
        let out = refs.map(input => input.current.value);
        out = out.filter(value => value.length > 0);

        return out.length > 0 ? out : null;
    }

    const getTopTracks = async () => {
        let cap = 3;
        const customSongInputs = getCustomInputs(customSongInputRefs);
        const customPlaylistInputs = getCustomInputs([customPlaylistRef]);
        if (seedType === '1 - 3 songs' && customSongInputs !== null) {
            let customids = getIDFromLinks(customSongInputs);
            getRecommendations(customids);
        }
        else if (seedType === 'a playlist' && customPlaylistInputs !== null) {
            let customid = getIDFromLinks(customPlaylistInputs)[0];
            getPlaylistTracks(customid);
        }
        else if (seedType === 'your top 3 songs') {
            const url = `${API_BASE}me/top/tracks/?limit=${cap}&time_range=short_term`;
            fetch(url, {
                method: 'GET',
                headers: getAuthHeaders(token),
            }).then(res => res.json()).then(res => {
                let items = res.items.slice(0, cap);
                getRecommendations(items.map(track => track.id));
            });
        }
    }

    const getRecommendations = async (trackids) => {
        const url = `${API_BASE}recommendations/?`;
        const params = querystring.stringify({
            seed_artists: '',
            seed_genres: '',
            seed_tracks: trackids,
            limit: 30
        });

        fetch(url+params, {
            method: 'GET',
            headers: getAuthHeaders(token),
        }).then(res => res.json()).then(res => {
            setTopTracks(res.tracks.map(track => (
                <li>{ track.name } - { track.artists[0].name }</li>
            )));

            getUser(res.tracks.map(track => track.uri));
        });
    }

    const createPlaylist = async (userid, trackuris) => {
        const url = `${API_BASE}users/${userid}/playlists`;
        const title = titleInput.current.value;
        fetch(url, {
            method: 'POST',
            body: JSON.stringify({
                name: title.length > 0 ? title : getToday(),
            }),
            headers: getAuthHeaders(token),
        }).then(res => res.json()).then(res => {
            addTracks(res.id, trackuris);
        });
    }

    const addTracks = async (playlistid, trackuris) => {
        const url = `${API_BASE}playlists/${playlistid}/tracks`;
        fetch(url, {
            method: 'POST',
            body: JSON.stringify({
                uris: trackuris,
            }),
            headers: getAuthHeaders(token),
        }).then(res => setResponse('A new playlist has been added to your account'));
    }

    const getPlaylistTracks = async (playlistid) => {
        const url = `${API_BASE}playlists/${playlistid}/tracks`;
        fetch(url, {
            method: 'GET',
            headers: getAuthHeaders(token),
        }).then(res => res.json()).then(res => {
            let trackids = res.items.map(item => item.track.id);
            getRecommendations(trackids);
        });
    }

    const seedTypeChange = () => {
        console.log(seedTypeInput.current.value);
        setSeedType(seedTypeInput.current.value);
    }

    const courierNew = { fontFamily: 'Courier New' };

    return (
        <div style={ courierNew }>
            <h1>Spotify Playlist Generator</h1>
            <div style={{ display: token ? '' : 'none' }}>
                Generate a new playlist based off your song preferences.
            </div>
            <div>
            { !token ?
                     <a href={ `${AUTH_ENDPOINT}?${QUERY_PARAMS}` }>Login
                        to Spotify to get started</a>
                     : '' }
            </div>
            <div style={{ display: token ? '' : 'none' }}>
                <div>
                    <div>
                        Choose&nbsp;
                        <select ref={ seedTypeInput } defaultValue="top 3 songs" onChange={ seedTypeChange }>
                            <option name="top3">your top 3 songs</option>
                            <option name="song">{ upToThree }</option>
                            <option name="playlist">a playlist</option>
                        </select>
                        &nbsp;to start the playlist&nbsp;

                        <span style={{ display: seedType !== top3 ? '' : 'none' }}>
                            <button style={ courierNew } onClick={ () => setHelp(!helpToggle) }>?</button>
                            <div>
                                <img style={{ display: helpToggle ? '' : 'none' }} src={ seedType === aPlaylist ? helpPlaylist : helpImage } />
                            </div>
                        </span>
                    </div>
                    <div style={{ display: seedType === upToThree ? '' : 'none' }}>
                        { customSongInputs.map(input => <div>{ input }</div>) }
                    </div>
                    <div style={{ display: seedType === aPlaylist ? '' : 'none' }}>
                        <input type="text" style={{ width: '13em' }} ref={ customPlaylistRef } placeholder="spotify playlist link" />
                    </div>
                </div>
                <div style={{ margin: '1em 0' }}>
                    Choose a title (default: today's date)
                    <div>
                        <input type="text" ref={ titleInput } placeholder="title" />
                    </div>
                </div>
                <div>
                    <button style={ courierNew } onClick={ getTopTracks }>Generate new playlist</button>
                </div>
                <div style={{ display: topTracks.length > 0 ? '' : 'none' }}>
                    Creating playlist with the following songs:
                    <ul>
                        { topTracks }
                    </ul>
                </div>
                <div>
                    { response }
                </div>
                <p>
                    <button style={ courierNew } onClick={ logout }>Logout</button>
                </p>
            </div>
        </div>
    );
}

export default App;
